body {
  margin: 0;
}
.landing__header {
  background-color: white;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  
  min-width: 320px;
  min-height: 70px;

  margin-left: auto;
  margin-right: auto;

  @media (min-width: 720px) {
    min-width: 720px;
  }
  @media (min-width: 1160px) {
    min-width: 1160px;
  }
}
.landing__header-container {
  margin-left: auto;
  margin-right: auto;

  min-width: 300px;
  @media (min-width: 720px) {
    min-width: 720px;
  }
  @media (min-width: 1160px) {
    max-width: 1160px;
    height: 64px;
    flex-direction: row;
  }
}
.landing__container {
  min-width: 320px;
  max-width: 1440px;
  min-height: 765px;

  margin-left: auto;
  margin-right: auto;
}
.landing__image-background {
  position: absolute;
  top: 0;
  z-index: -1;
}
.landing__content {
  position: relative;
  min-width: 320px;
  max-width: 1160px;
  min-height: 765px;
  padding-top: 70px;

  z-index: 1;
  margin-left: auto;
  margin-right: auto;
}
.landing__form-search-room {
  width: 300px;
  height: 375px;

  padding-top: 40px;
  padding-left: 27px;
  padding-right: 30px;
  padding-bottom: 30px;
  margin-left: auto;
  margin-right: auto;

  background-color: white;

  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  @media(min-width: 480px) {
    width: 380px;
    margin-left: 8%;
    margin-right: auto;
  }
  @media(min-width: 1160px) {
    margin-left: 7.5%;
    margin-right: 0;
  }
  @media(min-width: 1180px) {
    margin-left: 7%;
  }
  @media(min-width: 1200px) {
    margin-left: 6%;
  }
  @media(min-width: 1220px) {
    margin-left: 4%;
  }
  @media(min-width: 1240px) {
    margin-left: 2%;
  }
  @media(min-width: 1260px) {
    margin-left: 0;
  }
}
.landing__text {
  position: absolute;

  width: 280px;

  right: -5px;
  bottom: -45px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  
  text-align: right;
  
  color: rgba(31, 32, 65, 0.75);

  margin: 0;
  @media(min-width: 320px) {
    margin-right: 20px;
  }
  @media(min-width: 1160px) {
    margin-right: 0px;
  }
}
.landing__footer {
  margin-top: 65px;
  padding-top: 60px;
  padding-bottom: 60px;
}