.copyright {
  background-color: white;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  border-top: 1px solid rgba(31, 32, 65, 0.25);
}
.copyright__content {
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  min-width: 320px;
  height: 90px;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  align-items: center;

  @media (min-width: 720px) {
    min-width: 720px;
    height: 80px;
    flex-direction: row;
  }
  @media (min-width: 1160px) {
    width: 1160px;
  }
}
.copyright__description {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;

  color: rgba(31, 32, 65, 0.75);
  text-align: center;
  @media (min-width: 720px) {
    text-align: start;
  }
}
.copyright__social-btn {
  width: 110px;
}