*{
  box-sizing: border-box;
}
.rating {
  position: relative;
  display: flex;
  list-style: none;

  width: 120px;
  padding: 0;
  margin: 0;
}
.rating-item {
  width: 24px;
  height: 24px;
  cursor: pointer;
}
.rating-item::before {
  content: 'star_border';

  position: absolute;

  font-family: 'Material Icons';
  font-size: 22px;
  color: #BC9CFF;
}

.rating-item.active::after {
  content: 'star';

  position: absolute;

  font-family: 'Material Icons';
  font-size: 22px;
  color: #BC9CFF;
}