.room-cost-form {
  display: flex;
  flex-direction: column;
}
.room-cost-form__identification-room {
  display: flex;
  justify-content: space-between;

  width: 100%;
  margin-bottom: 13px;
}
.room-cost-form__specification-room {
  font-family: 'Quicksand';
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;

  color: #1F2041;
}
.room-cost-form__cost-per-day {
  padding-right: 3px;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 34px;

  color: rgba(31, 32, 65, 0.5);
}
.room-cost-form__inputs {
  width: 100%;
  height: 147px;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: space-between;

  margin-bottom: 20px;
}
.room-cost-form__input-container {
  width: 47%;
}
.room-cost-form__input-container:last-of-type {
  width: 100%;
}
.room-cost-form__calculation {
  width: 100%;
  height: 110px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 25px;
}
.room-cost-form__calculation-item {
  display: flex;
  justify-content: space-between;

  width: 100%;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;

  color: rgba(31, 32, 65, 0.75);
}
.room-cost-form__calculation-item-content_first {
  width: 115px;
}
.room-cost-form__calculation-item-content_second {
  width: 220px;
  display: flex;
  justify-content: space-between;
}
.room-cost-form__calculation-item-content_third {
  width: 220px;
}
.room-cost-form__calculation-item-content {
  max-width: 215px;
  @media(min-width: 380px) {
    max-width: 215px;
  }
}
.room-cost-form__total {
  display: flex;
  justify-content: space-between;

  width: 100%;
  height: 21px;

  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 23.5px;
}
.room-cost-form__total-item {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;

  color: #1F2041;
}
.room-cost-form__total-item_with-border {
  width: 120px;
  height: 18px;
  border-bottom: 1px dotted rgba(31, 32, 65, 0.25);
  @media(min-width: 380px) {
    width: 180px;
  }
}
.room-cost-form__total-item_with-cost {
  font-family: 'Quicksand';
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;

  color: #1F2041;
}
.room-cost-form__armor-button {
  width: 100%;
  height: 44px;
}